[tooltip]:hover:after {
  visibility: visible;
  opacity: 1;
}
[tooltip]:before {
  position: absolute;
  top: -13px;
  left: 50%;
  z-index: 99999;
  display: block;
  visibility: hidden;
  width: 0;
  height: 0;
  border-width: 7px 7px 0 7px;
