  border-color: var(--black) transparent transparent transparent;
  content: '';
  opacity: 0;
  transition: 0.4s;
  transform: translate(-50%, 0);
  user-select: none;
}
[tooltip]:after {
  position: absolute;
  top: -44px;
  left: 50%;
  z-index: 99999;
  display: block;
  visibility: hidden;
  padding: var(--padding);
  border-radius: var(--borderRadius);
  background-color: var(--black);
