  top: 50%;
  left: -12px;
  transform: translate(-100%, -50%);
}
[tooltip-position='right']:before {
  top: 50%;
  right: -13px;
  left: auto;
  left: initial;
  border-width: 7px 7px 7px 0;
  border-color: transparent var(--black) transparent transparent;
  transform: translate(0, -50%);
}
[tooltip-position='right']:after {
  top: 50%;
  right: -12px;
  left: auto;
  left: initial;
  transform: translate(100%, -50%);
